.slideshow {
  width: 100%;
  height: 415px;
  margin: 3.125rem auto;
  position: relative;
  overflow: hidden;
  border-radius: 1.5625rem;
}
.slideshow .slideshow-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 1.5625rem;
}

.prev,
.next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 6rem;
  height: 7.5rem;
  background: none;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.prev {
  left: 1rem;
}

.next {
  right: 1rem;
}

.counter {
  position: absolute;
  bottom: 1rem;
  right: 50%;
  transform: translateX(50%);
  background: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 0.2rem 0.5rem;
  border-radius: 0.3rem;
  font-size: 1rem;
}

.slideshow-empty {
  text-align: center;
  font-style: italic;
  color: #999;
  padding: 2rem 0;
}